import React, { Component } from 'react'
import styled from '@emotion/styled'
import { css } from '@emotion/core'
import { Tabs, Input, Icon } from 'antd'
import { Container, Typography } from 'reus'
import Layout from 'Containers/Layout'
import { StaticQuery, graphql, Link } from 'gatsby'
import TabContentList from './TabContentList'
import { FormattedMessage } from 'react-intl'

const TabContentWrapper = ({data}) => (
  <div css={css`margin-top: 30px;`}>
    {
      data.allMarkdownRemark && data.allMarkdownRemark.edges.map((item, i) => (
        <TabContentList key={i} data={item}/>
      ))
    }
  </div>
)

class HelpCenter extends Component {
  constructor(props) {
    super(props)
    this.state = {
      searchValue: '',
      query: ``,
      results: [],
      tabList: [
        {
          title: 'Account',
          subList: [
            {
              title: 'Akun diblokir',
              link: '/'
            },
            {
              title: 'Lupa kata sandi',
              link: '/'
            }
          ]
        },
        {
          title: 'Payroll',
          subList: [
            {
              title: 'Gaji tidak masuk',
              link: '/'
            },
            {
              title: 'History gaji tidak update',
              link: '/'
            }
          ]
        },
        {
          title: 'Attendance',
          subList: [
            {
              title: 'Absensi tidak terecord',
              link: '/'
            },
            {
              title: 'Saya terlambat/terlalu cepat clock on/clock off',
              link: '/'
            },
            {
              title: 'Bagaimana request lembur',
              link: '/'
            }
          ]
        },
        {
          title: 'Lending',
          subList: [
            {
              title: 'Request pinjaman saya ditolak',
              link: '/'
            },
            {
              title: 'Saya tidak bisa request pinjaman',
              link: '/'
            },
            {
              title: 'Bagaimana cara pembayaran',
              link: '/'
            },
            {
              title: 'Pembayaran saya tidak terautomasi',
              link: '/'
            }
          ]
        },
        {
          title: 'Modanaku-Wallet',
          subList: [
            {
              title: 'Saya tidak bisa top up wallet',
              link: '/'
            },
            {
              title: 'Saldo tidak bisa digunakan',
              link: '/'
            },
            {
              title: 'Saldo tidak bisa di transfer',
              link: '/'
            },
            {
              title: 'Saldo tidak bisa ditarik di ATM',
              link: '/'
            },
            {
              title: 'Saldo tidak muncul',
              link: '/'
            },
            {
              title: 'Saldo yang muncul tidak sesuai',
              link: '/'
            },
            {
              title: 'Saldo disalahgunakan pihak lain',
              link: '/'
            }
          ]
        }
      ]
    }
  }

  emitEmpty = () => {
    this.setState({searchValue: ''})
  }

  getSearchResults = (query) => {
    if (!query || !window.__LUNR__) return []
    const lunrIndex =  window.__LUNR__['en'];
    const results = lunrIndex.index.search(query) // you can  customize your search , see https://lunrjs.com/guides/searching.html
    return results.map(({ ref }) => lunrIndex.store[ref])
}

  onChangeSearch = e => {
    const q = e.target.value
    const results = this.getSearchResults(q)
    this.setState({searchValue: q, results})
  }

  handleClick = e => {
    e.currentTarget.childNodes[1].style.height = 'auto'
  }

  render() {
    const { TabPane } = Tabs
    const StyledBanner = styled.div`
      background: ${({theme}) => theme.color.primary};
      form {
        height: 300px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    `
    const suffix = this.state.searchValue ? <Icon type="close-circle" onClick={this.emitEmpty} /> : null;
    const Banner = () => (
      <div css={css`
        background: ${({theme}) => theme.color.primary};
        form {
          height: 300px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      `}>
        <Container md gutter={22}>
          <form>
            <Input 
              onChange={this.onChangeSearch} 
              value={this.state.searchValue}
              suffix={suffix} 
              placeholder={<FormattedMessage id="help-center-search-placeholder"/>}/>
          </form>
        </Container>
      </div>
    )

    const  TabsContent = ({data}) => (
      <Container gutter={22} md>
        <Tabs defaultActiveKey="1" css={theme => css`
          top: -44px;
          .ant-tabs-nav-scroll {
            text-align: center;
          }
          .ant-tabs-bar {
            border-bottom: none;
          }
          .ant-tabs-ink-bar-animated {
            background-color: ${theme.color.secondary};
          }
          .ant-tabs-tab {
            text-transform: uppercase;
            color: #fff;
            &.ant-tabs-tab-active {
              color: ${theme.color.secondary};
            }
          }
        `}>
          <TabPane tab="HR" key="1">
            <TabContentWrapper data={data}/>
          </TabPane>
          <TabPane tab="Employee" key="2">Content of Tab Pane 2</TabPane>
          <TabPane tab="Lenders" key="3">Content of Tab Pane 3</TabPane>
        </Tabs>
      </Container>
    )
    
    return (
      <StaticQuery
        query={graphql`
          query HelpCenterQuery {
            allMarkdownRemark(sort: { order: ASC, fields: [frontmatter___title]}) {
              edges {
                node {
                  fields {
                    slug
                  }
                  frontmatter {
                    title
                  }
                  headings {
                    value
                    depth
                  }
                }
              }
            }
          }
        `}
        render={data => {
          return (
            <Layout>
              <div css={theme => css`
                background: ${theme.color.primary};
                form {
                  height: 300px;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
              `}>
                <Container md gutter={22}>
                  <form>
                    <div>
                      <Typography
                        tag="h3"
                        type="headline"
                        color="#fff"
                        css={css`padding-bottom: 20px;`}
                      >
                        <FormattedMessage id="help-center-banner-title"/>
                      </Typography>
                      <div css={css`position: relative;`}>
                        <Input css={css`
                          input {
                            border-radius: 21px;
                          }
                        `}
                          prefix={<Icon type="search" />}
                          size="large"
                          onChange={this.onChangeSearch} 
                          value={this.state.searchValue}
                          suffix={suffix} 
                          placeholder="Type your question here..."
                        />
                        {
                          this.state.results.length ?
                            <div css={css`
                              position: absolute;
                              left: 0;
                              right: 0;
                              z-index: 10;
                              top: 60px;
                              ul {
                                list-style: none;
                                background: #fff;
                                border-radius: 20px;
                                padding: 13px 21px;
                                box-shadow: 0px 3px 24px #a0a0a06e;
                                li {
                                  padding: 10px 0;
                                  a {
                                    font-weight: bold;
                                    color: #333;
                                  }
                                  p {
                                    color: #717171;
                                  }
                                }
                              }
                            `}>
                              <ul>
                                {
                                  this.state.results.map(item => (
                                    <li>
                                      <Link to={item.slug}>{item.title}</Link>
                                    </li>
                                  ))
                                }
                              </ul>
                            </div> : null
                        }
                      </div>
                    </div>
                  </form>
                </Container>
              </div>
              <TabsContent data={data}/>
            </Layout>
          )
        }}
      />
    )
  }
}

export default HelpCenter